<template>
  <div class="container" id="full-screen">
    <div class="content">
      <div class="top">
        <div class="lottery-live-title" v-show="lotteryLiveTitleShow"></div>
        <div class="scan-title" v-show="scanTitleShow"></div>
        <div class="message-wall-title" v-show="messageWallTitleShow"></div>
        <div class="sponsor-school">
          <p
            :style="{ 'background-image': 'url(' + picUrl + ')','background-repeat':'no-repeat','background-size':'cover','background-position':'center center' }"></p>
        </div>
        <div class="campus-logo">
          <img src="../../assets/lotterywall_pc_code.png" alt="">
          <div>
            <p>扫码关注百度校园</p>
            <p>参与本次抽奖</p>
          </div>
        </div>
      </div>
      <div class="mid" v-show="lotteryLiveTitleShow">
        <div class="mid-left">
          <div>
            参加抽奖人数：{{totalNumber}}
          </div>
          <ul>
            <li class="item" v-for="(i,key) in allData" v-show="key == showNumber">
              <img :src="i.userEntity.headimgurl" alt="">
              <p>{{i.userEntity.nickname}}</p>
            </li>
          </ul>
          <div class="lottery-start" v-on:click="startLottery" v-show='start'></div>
          <div class="lottery-stop" v-on:click="stopLottery" v-show="stop"></div>
          <div class="choice-count">
            <h4>本次抽取</h4>
            <select v-model="choiceCount">
              <option value="1">1人</option>
              <option value="2">2人</option>
              <option value="3">3人</option>
              <option value="4">4人</option>
              <option value="5">5人</option>
            </select>
          </div>
        </div>
        <div class="mid-right">
          <p>获奖名单({{winnersList.length}})</p>
          <ul>
            <li class="list" v-for="i in winnersList">
              <img :src="i.userEntity.headimgurl" alt="">
              <p>{{i.userEntity.nickname}}</p>
            </li>
          </ul>
        </div>
      </div>
      <div class="code-page" v-show="scanTitleShow">
        <img src="../../assets/lotterywall_pc_code.png" alt="">
        <p>打开微信扫描二维码</p>
        <p>参与现场互动</p>
      </div>
      <!--消息墙列表-->
      <div class="messagewall-page" v-show="messageWallListShow">
        <ul v-show="!messageDetailShow">
          <li v-for="(i,key) in messageData" v-on:click="showMoreMessage(key)" :class="rotate?'rotate':''">
            <img :src="i.headimgurl" alt="">
            <p>
              <a>
                <span>{{i.nickname}}：</span>{{i.content}}
                <img v-if="i.msgType == 'image'" :src="i.picUrl" alt="">
              </a>
            </p>
            <div></div>
          </li>
        </ul>
      </div>
      <div class="message-detail" :class="messageDetailShow?'active':''"
           ref="messagewallPage">
        <div>
          <img :src="messageDetailData.headimgurl" alt="">
          <p>{{messageDetailData.nickname}}:</p>
        </div>
        <div v-if="messageDetailData.msgType == 'text'">
          {{messageDetailData.content}}
        </div>
        <img class="message-detail-img" :class="messageDetailShow?'img-active':''" :src="messageDetailData.picUrl"
             v-if="messageDetailData.msgType == 'image'"
             alt="">
        <div v-on:click="messageDetailClose"></div>
      </div>
      <div class="bottom">
        <div class="control">
          <img src="../../assets/lotterywall_next.png" alt="" v-on:click="pageTurning('next')">
          <img src="../../assets/lotterywall_message_stop.png" alt="" v-on:click="messageStop" v-if="!pause">
          <img src="../../assets/lotterywall_message_start.png" alt="" v-on:click="messageStart" v-if="pause">
          <img src="../../assets/lotterywall_last.png" alt="" v-on:click="pageTurning('last')">
          <img src="../../assets/lotterywall_full.png" alt="" v-on:click="launchFullscreen">
          <img src="../../assets/lotterywall_code.png" alt="" v-on:click="showCode">
          <img src="../../assets/lotterywall_messege.png" alt="" v-on:click="showMessage">
          <img src="../../assets/lotterywall_menu.png" alt="" v-on:click="showMenu">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "LotteryWall",
    data() {
      return {
        container: '',
        activityId: this.getQueryParams('id'),//活动id
        totalNumber: 0,//总人数
        allData: [],//参加抽奖的所有人头像名字
        luckData: [],//中奖的人
        unChangeData: [],//参加抽奖的所有人头像名字（存储起来）
        winnersList: [],//抽奖名单
        messageData: [],//消息列表
        messageDetailData: {},//消息详情
        showNumber: 0,
        start: true,//开始抽奖
        stop: false,//暂停抽奖
        imgChange: '',//头像变化时间函数
        flag: true, //开始抽奖按钮不能连续点击
        scanTitleShow: false,//扫码互动
        lotteryLiveTitleShow: true,//现场抽奖
        messageWallTitleShow: false,//消息墙
        messageWallListShow: false,//消息列表
        messageDetailShow: false,//消息闲情
        msgId: 0,//消息id
        next: -1,//翻页
        refresh: '',//刷新消息时间函数
        nextPageFlag: true,
        pause: true,//消息暂停按钮 暂停
        choiceCount: '1',//选择中奖人数
        picUrl: '',//学校图片
        rotate: false,
        canClick: true,
        isClick: '',
        timer: '',
      }
    },
    mounted() {
      var element = document.getElementById('full-screen')
      //活动人数
      this.total();
      //抽奖头像
      axios.post(this.pc_proxy + 'luckDrawImg.do?activityId=' + this.activityId + '&num=50', {}).then(res => {
        if (res.status == 200 && res.data.error_no == '0') {
          this.allData = res.data.data;
          this.unChangeData = res.data.data;
        }
      })
      axios.get(this.pc_proxy + 'queryActivity.do?activityId=' + this.activityId, {}).then(res => {
        if (res.status == 200 && res.data.error_no == '0') {
          this.picUrl = res.data.data.pic_url;
        }
      })
    },
    methods: {
      total() {
        axios.post(this.pc_proxy + 'queryPcActivityUserTotal.do?activityId=' + this.activityId, {}).then(res => {
          if (res.data.error_no == '0') {
            this.totalNumber = res.data.data;
            setTimeout(res => {
              this.total();
            }, 5000)
          }
        })
      },
      launchFullscreen() {
        var element = document.getElementById('full-screen')
        if (
          document.fullscreenElement ||
          document.webkitFullscreenElement ||
          document.mozFullScreenElement ||
          document.msFullscreenElement
        ) {
          if (document.exitFullscreen) {
            document.exitFullscreen();
          } else if (document.webkitExitFullscreen) {
            document.webkitExitFullscreen();
          } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen();
          } else if (document.msExitFullscreen) {
            document.msExitFullscreen();
          }
        } else {
          if (element.requestFullscreen) {
            element.requestFullscreen();
          } else if (element.mozRequestFullScreen) {
            element.mozRequestFullScreen();
          } else if (element.msRequestFullscreen) {
            element.msRequestFullscreen();
          } else if (element.webkitRequestFullscreen) {
            element.webkitRequestFullScreen();
          }
        }
      },
      startLottery() {
        if (this.flag) {
          this.flag = false;
          this.allData = this.unChangeData;
          axios.post(this.pc_proxy + 'lotteryDraw.do?activityId=' + this.activityId + '&num=' + this.choiceCount, {}).then(res => {
            if (res.data.error_no == '0' && res.status == 200) {
              this.start = false;
              this.stop = true;
              this.luckData = res.data.data;
              this.imgChange = setInterval(res => {
                this.showNumber++
                if (this.showNumber == this.allData.length) {
                  this.showNumber = 0;
                }
              }, 100)
            } else if (res.data.error_no == '1' && res.data.data.code == '100009') {
              alert(res.data.data.message)
            }
          })
        }
      },
      stopLottery() {
        this.stop = false;
        this.start = true;
        setTimeout(res => {
          this.showNumber = 0;
          this.allData = this.luckData;
          for (var i = 0; i < this.luckData.length; i++) {
            this.winnersList.push(this.luckData[i]);
          }
          clearInterval(this.imgChange)
          this.flag = true;
        }, 500)
      },
      showMessage() {
        if (!this.messageWallTitleShow) {
          this.initialization();
          this.pause = false;
          this.messageWallTitleShow = true;
          this.messageWallListShow = true;
          this.showOnTheWall();
        }
      },
      showCode() {
        this.initialization()
        this.pause = true;
        this.scanTitleShow = true;
      },
      showOnTheWall(str) {
        if(!this.pause){
          axios.post(this.pc_proxy + 'queryRelayUsers.do?activityId=' + this.activityId + '&limit=3&' + 'msgId=' + this.msgId + '&next=' + this.next, {}).then(res => {
            if (res.status == 200 && res.data.error_no == 0) {
              this.rotate = true;
              if (res.data.data.length == 3) {
                this.messageData = res.data.data;
                this.msgId = res.data.data[0].msgId;
                for (var i = 0; i < res.data.data.length; i++) {
                  if (this.msgId < res.data.data[i].msgId) {
                    this.msgId = res.data.data[i].msgId;
                  }
                }
                this.next = 1;
              } else {
                this.msgId = 0;
                this.next = -1;
              }
              if (str != 'next' && str != 'last') {
                clearTimeout(this.timer);
                this.refresh = setTimeout(res => {
                  this.rotate = false;
                  this.isClick = setTimeout(() => {
                    this.showOnTheWall()
                  }, 3000)
                }, 1000)
              } else {
                setTimeout(() => {
                  this.rotate = false
                }, 1000)
              }
            }
          })
        }
      },
      pageTurning(str) {
        if (this.messageWallListShow && !this.rotate) {
          if(this.pause){
            this.pause = false;
          }
          clearTimeout(this.refresh);
          clearTimeout(this.isClick);
          clearTimeout(this.timer);
          this.timer = setTimeout(() => {
            this.showOnTheWall()
          }, 4000);
            if (str == 'next') {
              this.next = 1;
              this.msgId = 0;
              for (var i = 0; i < this.messageData.length; i++) {
                if (this.messageData[i].msgId >= this.msgId) {
                  this.msgId = this.messageData[i].msgId;
                }
              }
            }
            if (str == 'last') {
              this.next = -1;
              this.msgId = this.messageData[0].msgId;
              for (var i = 0; i < this.messageData.length; i++) {
                if (this.messageData[i].msgId <= this.msgId) {
                  this.msgId = this.messageData[i].msgId;
                }
              }
            }
            this.showOnTheWall(str)
        }
      },
      messageStop() {
        if (this.messageWallListShow) {
          this.pause = true;
          clearTimeout(this.refresh);
          clearTimeout(this.isClick);
          clearTimeout(this.timer);

        }
      },
      messageStart() {
        if (this.messageWallListShow) {
          this.pause = false;
          this.showOnTheWall()
        }
      },
      showMoreMessage(key) {
        this.initialization();
        this.pause = true;
        this.messageDetailShow = true;
        this.messageWallTitleShow = true;
        this.messageDetailData = this.messageData[key];
      },
      messageDetailClose() {
        this.initialization();
        this.pause = false;
        this.refresh = setTimeout(res => {
          this.showOnTheWall()
        }, 4000)
        this.messageWallListShow = true;
        this.messageWallTitleShow = true;
      },
      showMenu() {
        this.pause = true;
        this.initialization();
        this.lotteryLiveTitleShow = true;
      },
      initialization() {
        this.scanTitleShow = false//扫码互动
        this.lotteryLiveTitleShow = false//现场抽奖
        this.messageWallTitleShow = false//消息墙
        this.messageWallListShow = false//消息列表
        this.messageDetailShow = false//消息闲情
        clearTimeout(this.refresh);
        clearTimeout(this.isClick);
        clearTimeout(this.timer);
      }
    }
  }
</script>

